<script>
	import https from '../api/https'
	export default {
		name: 'Home',
		data: function(){
			return {
				showLoading: true,
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
				swiperOption: {
					loop: true,
					autoplay: {
						delay: 3000,
						stopOnLastSlide: false,
						disableOnInteraction: false,
					},
					pagination: {
						el: '.swiper-pagination',
						// type: 'custom'
					},
				},
				showNoticeBox: false,
				noticeData: "",

				my: null,
				sys: null,
				showHelpBox: false,
				helpData: null,
			}
		},
		created(){
			this.getHomeData()
			this.getHelpData()
			this.getNoticeData()
			console.log(this.$route.name)
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			this.bgWrapHeight = viwePortHeight

			console.log('VHinvite_code',this.$route.params.VHinvite_code)
			if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
			if(this.VHinvite_code) {
				let firstChart = this.VHinvite_code.split('')[0]
				if((firstChart >= 'a' && firstChart <='z') || (firstChart >='A' && firstChart <= 'Z')) {
					localStorage.setItem('appChannel',this.VHinvite_code)
				}
			}
		},
		mounted: function () {
			
		},
		methods: {
			getHomeData: function(){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/video/home').then((data) => {
					this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						// 
						this.my = res.data.my
						console.log('my',this.my)
						this.sys = res.data.sys
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
						this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getHomeData()
					}
				})
			},
			goApp(){
				if(localStorage.getItem('user-token')) {
					if(localStorage.getItem('appChannel')) {
						this.$router.push('/app/'+localStorage.getItem('appChannel'))
					}else {
						this.$router.push('/app')
					}
				}else {
					this.goPage('/Login')
				}
			},
			goPage(page){
				if(!localStorage.getItem('user-token')) {
					page = '/Login'
				}
				if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			openWindow(type,account) {
				let baseHelpUrl = ''
				if(type == 'whatsapp') {
					baseHelpUrl = "https://wa.me/" + account
				}else if(type == 'telegram'){
					baseHelpUrl = "https://t.me/" + account
				}
				window.open(baseHelpUrl)
			},
			clickHelp(){
				this.showHelpBox = true
				this.getHelpData()
			},
			getHelpData: function(){
				https.fetchGet('/api/user/contact').then((data) => {
					let res = data.data
					if(res.code == 0) {
						// 
						this.helpData = res.data
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
						this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getHelpData()
					}
				})
			},
			getNoticeData: function(){
				https.fetchGet('/api/user/popup').then((data) => {
					let res = data.data
					if(res.code == 0) {
						// 
						this.noticeData = res.data
						if(this.noticeData.content && this.noticeData.updatetime) {
							if(!localStorage.getItem('updatetime') || this.noticeData.updatetime != localStorage.getItem('updatetime')) {
								localStorage.setItem('updatetime', this.noticeData.updatetime)
								let newDate = new Date()
								let NowTime = newDate.getTime()
								localStorage.setItem('noticeTime',NowTime)
								this.showNoticeBox = true
								return
							}

							if(!localStorage.getItem('user-token')) {
								if(sessionStorage.getItem('isSendNotice')) {
									return
								}else {
									sessionStorage.setItem("isSendNotice",true)
									this.showNoticeBox = true
									return
								}
							}
							let newDate = new Date()
							let NowTime = newDate.getTime()
							let oldTime = localStorage.getItem('noticeTime') || null
							let judgmentTime = null
							if(oldTime) {
								judgmentTime = (NowTime - oldTime) / (1000*60*60) 
								if(judgmentTime > 20) {
									localStorage.setItem('noticeTime',NowTime)
									this.showNoticeBox = true
								}
							}else{
								localStorage.setItem('noticeTime',NowTime)
								this.showNoticeBox = true
							}
							console.log(judgmentTime)
						}
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
						this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getNoticeData()
					}
				})
			},
		},
		computed: {
			
		},
		beforeDestroy() {
		},
	}
</script>
